import api from "../../../../../../constants/api";
import Service from "../../../../../../services/request";
export const getData = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_PRODUCT_DEMO.method,
      path: api.LIST_PRODUCT_DEMO.path,
      data: params,
    }).then((response) => {
      dispatch({
        type: "GET_DATA_PRODUCT_DEMO",
        data: response?.data,
        params,
      });
    });
  };
};

export const getById = (data) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_PRODUCT_DEMO.method,
      path: api.GET_PRODUCT_DEMO.path,
      query: data,
    })
      .then((response) => {
        dispatch({
          type: "GET_PRODUCT_DEMO",
          selected: response?.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };
};

export const add = (staff) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_PRODUCT_DEMO.method,
      path: api.CREATE_PRODUCT_DEMO.path,
      data: staff,
    })
      .then((response) => {
        dispatch({
          type: "ADD_PRODUCT_DEMO",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "ADD_PRODUCT_DEMO",
          err,
        })
      );
  };
};
export const update = (staff) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_PRODUCT_DEMO.method,
      path: api.UPDATE_PRODUCT_DEMO.path,
      data: staff,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_PRODUCT_DEMO",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_PRODUCT_DEMO",
          err,
        })
      );
  };
};
export const remove = (staff) => {
  return async (dispatch) => {
    await Service.send({
      method: api.DELETE_PRODUCT_DEMO.method,
      path: api.DELETE_PRODUCT_DEMO.path,
      data: staff,
    })
      .then((response) => {
        dispatch({
          type: "DELETE_PRODUCT_DEMO",
          response,
        });
      })
      .catch((err) => console.log(err));
  };
};
