import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import { update } from "../store/action";
import { htmlToDraftUtil } from "../../../../../utility/Utils";
import { convertToRaw, EditorState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { Editor } from "react-draft-wysiwyg";
import avatarBlank from "./../../../../../assets/images/avatars/bg-blank.png";
import { convertFileToBase64, uploadImage } from "../../../../../helper/common";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";

const CloudDemoTab = ({ selected }) => {
  const lang = useSelector((state) => state.common.language);

  const cate = yup.object({
    priority_index: yup
      .number()
      .typeError(
        <FormattedMessage id="The ordinal number field must be numeric" />
      )
      .required(<FormattedMessage id="The ordinal number field is required" />)
      .min(0, <FormattedMessage id="The ordinal number must be at least 0" />),
    short_description: yup
      .string()
      .required(<FormattedMessage id="The title field is required" />),
    demo_url: yup
      .string()
      .required(<FormattedMessage id="Link Demo is required" />),
    sample_code: yup
      .string()
      .required(<FormattedMessage id="Link Sample is required" />),
  });
  const { register, errors, handleSubmit } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });
  const [demoData, setDemoData] = useState(null);

  const store = useSelector((state) => state.discounts);
  const dispatch = useDispatch();
  const history = useHistory();
  const [disable, setDisable] = useState(false);

  const [valueDescription, setValueDescription] = useState(
    EditorState.createEmpty()
  );

  const [avatar, setAvatar] = useState(avatarBlank);
  const [fileImage, setFileImage] = useState();

  const onChange = async (e) => {
    const imageBase64 = await convertFileToBase64(e.target.files[0]);
    setFileImage(e.target.files[0]);
    setAvatar(imageBase64);
  };
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null && demoData !== null && selected.id !== demoData.id)
    ) {
      setDemoData(selected);
      setValueDescription(htmlToDraftUtil(selected?.long_description || " "));
      setAvatar(selected?.image || avatarBlank);
    }
  }, [selected]);

  const onSubmit = async (values) => {
    const demo_description = draftToHtml(
      convertToRaw(valueDescription.getCurrentContent())
    );

    if (isObjEmpty(errors)) {
      setDisable(true);
      var urlImage = "";
      if (avatar) {
        const newData = avatar.replace(/,/gi, "").split("base64");
        if (newData[1]) {
          const data = {
            imageData: newData[1],
            imageFormat: fileImage.type.split("/")[1],
          };
          const res = await uploadImage(data, fileImage).then((res) => {
            urlImage = res?.data;
          });
        } else {
          urlImage = avatar;
        }
        if (urlImage) {
          dispatch(
            update({
              id: parseInt(demoData?.id),
              data: {
                priority_index: parseInt(values?.priority_index),
                productId: parseInt(demoData?.productId),
                short_description: values?.short_description,
                long_description: demo_description,
                image: urlImage,
                lang: lang,
                demo_url: values?.demo_url,
                sample_code: values?.sample_code,
              },
            })
          );
        }
      }
    }
  };

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.username} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <div className="mt-1"></div>

          <FormGroup>
            <Label for="priority_index">
              <FormattedMessage id="index" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="priority_index"
              id="priority_index"
              placeholder=" "
              innerRef={register}
              onBlur={() => {
                let linkD = document.getElementById("priority_index");
                if (linkD && linkD.value) {
                  linkD.value = linkD.value.trim();
                }
              }}
              className={classNames({ "is-invalid": errors["last_name"] })}
              defaultValue={demoData?.priority_index}
            />
            <small className="text-danger">
              {errors?.priority_index && errors.priority_index.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="title">
              <FormattedMessage id="Title" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="short_description"
              id="short_description"
              innerRef={register}
              onBlur={() => {
                let discount = document.getElementById("short_description");
                if (discount && discount.value) {
                  discount.value = discount.value.trim();
                }
              }}
              className={classNames({
                "is-invalid": errors["short_description"],
              })}
              defaultValue={demoData && demoData.short_description}
            />
            <small className="text-danger">
              {errors?.short_description && errors.short_description.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="demo_url">
              <FormattedMessage id="Link Demo" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="demo_url"
              id="demo_url"
              innerRef={register}
              onBlur={() => {
                let discount = document.getElementById("demo_url");
                if (discount && discount.value) {
                  discount.value = discount.value.trim();
                }
              }}
              className={classNames({ "is-invalid": errors["demo_url"] })}
              defaultValue={demoData && demoData.demo_url}
            />
            <small className="text-danger">
              {errors?.demo_url && errors.demo_url.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="sample_code">
              <FormattedMessage id="Link Sample" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="sample_code"
              id="sample_code"
              innerRef={register}
              onBlur={() => {
                let discount = document.getElementById("sample_code");
                if (discount && discount.value) {
                  discount.value = discount.value.trim();
                }
              }}
              className={classNames({ "is-invalid": errors["sample_code"] })}
              defaultValue={demoData && demoData.sample_code}
            />
            <small className="text-danger">
              {errors?.sample_code && errors.sample_code.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="long_description">
              <FormattedMessage id="Description" />
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                options: ["inline", "textAlign"],
                inline: {
                  inDropdown: false,
                  options: ["bold", "italic", "underline"],
                },
              }}
              editorState={valueDescription}
              onEditorStateChange={(data) => setValueDescription(data)}
              name="long_description"
              innerRef={register}
            />
          </FormGroup>

          <FormGroup>
            <Label for="Image">
              <FormattedMessage id="Image" />
              <span className="text-danger"> *</span>
            </Label>
            <Row className="align-items-end p-1">
              <Media className="mr-25" left>
                <Media
                  object
                  className="rounded mr-50"
                  src={avatar}
                  height="100"
                  width="100"
                />
              </Media>{" "}
              <Row className="flex-column px-1">
                <Button.Ripple tag={Label} className="mt-1" color="primary">
                  <FormattedMessage id="Upload" />
                  <Input
                    type="file"
                    onChange={(e) => onChange(e)}
                    hidden
                    accept="image/*"
                  />
                </Button.Ripple>
              </Row>
            </Row>
          </FormGroup>

          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CloudDemoTab);
